import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withStyles, Typography, Icon, Grid } from '@material-ui/core';
import MailOutlineIcon from '@material-ui/icons/MailOutline';
import { Link } from 'react-router-dom';
import { userActions } from '../../auth/store/actions/user.actions';

import { styles } from '../index';

function ConfirmationEmail(props) {
	const { classes } = props;
	const [confirmCode, setConfirmCode] = React.useState(null);

	React.useEffect(() => {
		let confirmationCode = props.location.pathname.split('/')[3];
		if (confirmationCode) {
			setConfirmCode(confirmationCode);
			props.handleConfirm(confirmationCode);
		}
	}, []);

	const renderAccountConfirm = () => {
		const styleP = { textAlign: 'center', marginBottom: '1.6rem' };
		const styleLast = { textAlign: 'center' };
		return (
			<React.Fragment>
				<div style={{ margin: '20px' }}>
					<MailOutlineIcon color='action' style={{ fontSize: '9.5rem' }} />
				</div>

				<Typography variant='h5' style={styleP}>
					Confirm your email address!
				</Typography>

				<Typography style={styleP} color='textSecondary'>
					A confirmation e-mail has been sent to <b>{props.location.email}</b>.
				</Typography>

				<Typography style={styleLast} color='textSecondary'>
					Check your inbox and click on the "Confirm my email" link to confirm your email address.
				</Typography>
			</React.Fragment>
		);
	};

	const renderConFirmedMessage = () => {
		return <p>Your account has been activated {props.loadingConfirm}</p>;
	};

	return (
		<Grid container className={classes.rootCard}>
			<Grid item xs={12} sm={8} md={4} className={classes.rightPanel}>
				{confirmCode ? renderConFirmedMessage() : renderAccountConfirm()}
				<div style={{ paddingTop: '1rem', paddingBottom: '1rem' }}>
					<Link className={classes.links} to='/login'>
						Go back to login
					</Link>
				</div>
			</Grid>
		</Grid>
	);
}

const mapStateToProps = state => {
	return {
		loadingConfirm: state.authReducers.loadingConfirm,
		userConfirm: state.authReducers.userConfirm
	};
};
const mapDispatchToProps = dispatch => {
	return {
		handleConfirm: idConfirm => {
			dispatch(userActions.ConfirmAccount({ idConfirm: idConfirm }));
		}
	};
};

export default connect(
	mapStateToProps,
	mapDispatchToProps
)(withStyles(styles, { withTheme: true })(ConfirmationEmail));
